@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap');
*{
    margin: 0;
    padding: 0;
}


a{
    color: #2980b9;
}

body{
    font-family: 'Roboto', sans-serif;
}

.p-t-b-25{
    padding: 40px 0px 25px 0px;
}
.title-heading{
color:#ED4C67
}


.stats-title {
    font-size: 15px;
    font-weight: 600;
    color: #2c2c54;
}

.stats-p {
    padding-top:20px;
}


.death{
    background: #c0392b !important;
}

.today{
    background: #34495e !important;
}

.active{
    background: #f39c12 !important;
}

.recovered{
    background: #27ae60 !important;
}


.stats {
    width: 100px;
    height: 100px;
    background: #8e44ad;
    display: block;
    border-radius: 50%;
    margin: 0 auto;
}

.stats p {
    color: #fff;
    font-size: 50px;
    position: relative;
    top: 12px;
    font-weight: 500;
}


.header{
    color: #fff;
    background: #2c3e50;
}

.bg-dark {
    background-color: #2c3e50!important;
}

.stats-pp{
    margin-top: 100px;
    font-size: 18px;
}

.whole-world ul li {
    margin-bottom: 15px;
}

.whole-world ul li span{
    padding: 3px;
}

.whole-world ul {
    margin-left: 20px;
}


.footer{
    padding-top: 50px;
}

.footer-credites p {
    color: #7f8c8d;
}

@media only screen and (max-width: 600px) {
    .stats{
        margin-bottom: 50px;
    }
    .stats-title {
        font-size: 18px;
        font-weight: 400;
        color: #2c2c54;
    }
    .stats-pp {
        margin-top: 10px;
    }
}